import { h } from 'preact';
import { route } from 'preact-router';
import { Stores } from '../../stores/stores';
import { useEffect } from 'preact/hooks';
import { InputLogic } from '../../input/inputlogic';
import ExpenseInput from '../../components/ExpenseInput';

const LifeStyle = () => {

	useEffect(() => {
		InputLogic.focusFirstInputElement()
	}, []);

    return (
		<div style="padding:10px;">
			<p style="height:50px;"/>

			<div class="container">
				<hr/>
				<h5>LifeStyle</h5>
				Not all the expenses here apply to your situation, only check on those item that is applicable. The expense amount can be estimates. Click next when you are done.<p/>
				<div class="row align-items-start">
					<div class="col">
						<ExpenseInput store={Stores.lifestyle.travel} title="Travel" min_value="0" max_value="500000"
							description="Estimated budget for travel including flight/hotel/cruise/packages."/>

					</div>
					<div class="col">
						<ExpenseInput store={Stores.lifestyle.hobby} title="Hobby" min_value="0" max_value="50000"
							description="Hobby related expenses."/>
					</div>
				</div>
				<div class="row align-items-start">
					<div class="col">
						<ExpenseInput store={Stores.lifestyle.hair_stylist} title="Hair Stylist" min_value="0" max_value="20000"
							description="Hair stylist / nail work."/>
					</div>
					<div class="col">
						<ExpenseInput store={Stores.lifestyle.cosmetic_beauty} title="Cosmetic / Beauty" min_value="0" max_value="20000"
							description="Expenses on cosmetic and beauty products."/>
					</div>
				</div>	
				<div class="row align-items-start">
					<div class="col">
						<ExpenseInput store={Stores.lifestyle.clothing} title="Clothing" min_value="0" max_value="5000"
							description="New clothing / shoes / handbag purchases."/>
					</div>
					<div class="col">
						<ExpenseInput store={Stores.lifestyle.gift} title="Gifts" min_value="0" max_value="20000"
							description="Gifts for others during special occassion or holidays."/>
					</div>
				</div>		
				<div class="row align-items-start">
					<div class="col">
						<ExpenseInput store={Stores.lifestyle.gym_membership} title="Membership / Pass" min_value="0" max_value="5000"
							description="Gym membership, sports fees, yoga classes, ski pass, martial arts etc."/>
					</div>
					<div class="col">
						<ExpenseInput store={Stores.lifestyle.theater} title="Theater/ Show/ Live sport" min_value="0" max_value="20000"
							description="Expenses related to theater / live show / live sports etc."/>
					</div>
				</div>	    
				<div class="row align-items-start">
					<div class="col">
                        <ExpenseInput store={Stores.lifestyle.membership_fees} title="Annual membership" min_value="0" max_value="20000"
							description="Annual membership fees such as credit card annual fees."/>
					</div>
					<div class="col">
                        <ExpenseInput store={Stores.lifestyle.other} title="Other lifestyle" min_value="0" max_value="20000"
							description="Other expenses related to lifestyle."/>                        
					</div>
				</div>	
                <hr/>
                    <button type="button" class="btn btn-lg btn-primary" onClick={() =>route('/expense/transport')}>Back: Transportation</button>&nbsp;
                    <button type="button" class="btn btn-lg btn-primary" onClick={() =>route('/expense/analysis')}>Analysis and Recommendations</button>
				<hr/>                                             					
			</div>
		</div>
		
	);
};

export default LifeStyle;
